        console.log(await index.getTriples())
        console.log(await index.getRef())
        index.addNodeRef('http://www.w3.org/2005/01/wf/flow#message', subj)
        //https://solidarity.inrupt.net/public/ChatTest/index.ttl#this
        await chatDoc2.save();*/

        this.message=""
      }else{
